import React from "react";
import PropTypes from "prop-types";
// movie component does not need state
import "./Movie.css"; // no need to import any finction or anything

function Movie({ year, title, summary, poster, genres}) {
    /*
    Warning: Failed prop type: The prop `genres` is marked as required in `Movie`, but its value is `undefined`.
    in Movie (at App.js:36)
    in App (at src/index.js:7)
    App.js 에서 genres={movie.genres} 를 해줘야댐 
    */
    return (
    <div className="movie">
        <img src={poster} alt={title} title={title}/>
        <div className="movie__data"> 
            <h3 className="movie__title" /*style={{backgroundColor: "cyan"}}*/>{title}</h3>
            <h5 className="movie__year">{year}</h5>
            <ul className="movie__genres">
                {genres.map((genre, index) => (
                <li key={index} className="genres__genre">
                    {genre}
                </li>
            ))}
            </ul>
            <p className="movie__summary">{summary.slice(0, 200)}...</p>
        </div>
    </div>
    );
};

Movie.propTypes = {
    id: PropTypes.number.isRequired,
    year: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    summary: PropTypes.string.isRequired,
    poster: PropTypes.string.isRequired,
    genres: PropTypes.arrayOf(PropTypes.string).isRequired 
};

export default Movie;