
import React from "react";
import axios from "axios";
import Movie from "./Movie";
import "./App.css"; // no need to import any finction or anything

class App extends React.Component{ 
  state = {
    isLoading: true, 
    movies: []    
  };
  
getMovies = async () =>{ 
  
  const {data:{ data : {movies}}} = await axios
  .get("https://yts-proxy.now.sh/list_movies.json?sort by=rating");
 
  this.setState({movies, isLoading: false});
};

  componentDidMount() {
    this.getMovies();
}  

  render() { 
    const {isLoading, movies} = this.state; 
    return (
      <section className/*JS파일 안에서는 className이라고 해야댐*/="container"> 
        {isLoading ? (    
          <div className="loader">
             <span className="loader__text">Loading...</span>
          </div>
        ) : (
            // Each child in a list should have a unique "key"
             <div className="movies">
              {movies.map(movie => (
                <Movie  
                 key={movie.id}
                 id={movie.id}
                 year={movie.year} 
                 title={movie.title} 
                 summary={movie.summary} 
                 poster={movie.medium_cover_image} 
                 genres={movie.genres}
                />              
            ))}
             </div>
        )}
      </section>
    );   
  }
}

export default App;

  
